@media (min-width: 992px) {
  .article-border {
    position: relative;
  }
  .article-border:before, .article-border:after, .article-border > :first-child:before, .article-border > :first-child:after {
    transition: all 0.25s ease;
    position: absolute;
    width: 32px;
    height: 32px;
    border-color: #1C1C1C;
    border-style: solid;
    /* or whatever style */
    content: ' ';
  }
  .article-border:before {
    top: 0;
    left: 0;
    border-width: 1px 0 0 1px;
    border-top-left-radius: 12px;
  }
  .article-border:after {
    top: 0;
    right: 0;
    border-width: 1px 1px 0 0;
    border-top-right-radius: 12px;
  }
  .article-border > :first-child:before {
    bottom: 0;
    right: 0;
    border-width: 0 1px 1px 0;
    border-bottom-right-radius: 12px;
  }
  .article-border > :first-child:after {
    bottom: 0;
    left: 0;
    border-width: 0 0 1px 1px;
    border-bottom-left-radius: 12px;
  }
  .article-border:hover:before, .article-border:hover:after, .article-border:hover > :first-child:before, .article-border:hover > :first-child:after {
    border-color: #62DB54;
  }
}

